.statsSection {
    max-width: 375px;
    margin: 0 auto;
    margin: 20px 200px 40px;
    background-color: #f5f5f5;
    padding: 20px 0;
  }
  
  .title {
    margin: 0 auto;
    max-width: 268px;
    text-transform: uppercase;
    text-align: center;
    background-color: #ffffff;
    color: #ced1d4;
    padding: 20px;
  }
  .statsList {
    list-style: none;
    display: flex;
    justify-content: center;
    max-width: 260px;
    margin: 0 auto;
    padding: 0 20px;
    color: #ffffff;
  }
  .item {
    display: flex;
    flex-direction: column;
    padding: 10px;
    align-items: center;
    max-width: 30%;
    font-family: Arial, Helvetica, sans-serif;
  }
  .label,
  .percentage {
    padding: 10px 5px;
  }
  