.item {
    display: flex;
    align-items: center;
    flex-basis: 31%;
    margin-top: 14px;
    margin-right: 3.5%;
    padding: 10px;
    background: rgb(248, 248, 248);
    transition: 0.3s;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid rgba(0,0,0,0);
  }
  
  .item:hover {
    background: rgb(236, 236, 236);
    border: 1px solid rgb(206, 206, 206);
  }
  
  .item:nth-of-type(3n + 3) {
    margin-right: 0;
  }
  
  .online,
  .offline {
    width: 4px;
    height: 100%;
    border-radius: 4px;
    background-color: rgb(126, 212, 137);
  }
  
  .offline {
    background-color: rgb(247, 155, 148);
  }
  
  .avatar {
    width: 70px;
    margin: 0 10px;
    border-radius: 4px;
  }
  
  .name {
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    color: rgb(34, 34, 34);
  }
  